import Vue from 'vue'
import Router from 'vue-router'
import PuniuHome from '@/components/puniuHome'
import HelloWorld from '@/components/HelloWorld'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'PuniuHome',
      component: PuniuHome
    }, {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



